import { ChangeDetectorRef, Component, OnInit } from "@angular/core";
import { Subject } from "rxjs";
import { ApiService } from "src/app/shared/services/api.service";

@Component({
  selector: "npr-process-monitoring",
  templateUrl: "./process-monitoring.component.html",
  styleUrls: ["./process-monitoring.component.scss"],
})
export class ProcessMonitoringComponent implements OnInit {
  respArray: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  isLoading:boolean = false;

  constructor(private apiService: ApiService, private cdr:ChangeDetectorRef) {}

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      columnDefs: [ { type: 'date', 'targets': [7] } ],
      order: [[7, 'desc']],
      processing: true,
      lengthMenu: [[5, 10, 20, -1], [5, 10, 20, 30]],
      // columnDefs: [ { type: 'date', 'targets': [5] } ],
      // order: [[5, 'desc']],

    };

    this.getDataForMonitor();
  }

  getDataForMonitor() {
    this.isLoading = true;
    this.apiService.getMonitorData().subscribe((dataresp) => {
      console.log(dataresp);
      this.respArray = dataresp.result;
      this.isLoading = false;
      this.cdr.markForCheck();

      this.dtTrigger.next();
    });
  }


  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
}
